<template>
  <div class=''>

    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">

      <!--  -->
      <el-row>
        <el-col :span='8'>
          <el-input
          v-model="queryInfo.query" placeholder=""
          clearable @clear="getOrdersList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="ordersList" border stripe>
        <el-table-column type="index" label="#" />

        <el-table-column label="" prop="order_number" />

        <el-table-column label="" prop="order_price"  width="110px" />

        <el-table-column label="" width="90px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status ==='1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>

        <el-table-column label="" prop="is_send" width="90px" />

        <el-table-column label="" width="160px">
          <template slot-scope="scope">
          {{scope.row.create_time | dataFormat}}
          </template>
        </el-table-column>

        <el-table-column label="" width="120px">
          <template slot-scope="scope">
              <!--  -->
              <el-button
              type="primary" icon="el-icon-edit" size="mini"
              @click="addressDialogVisible = true"
              ></el-button>
              <!--  -->
              <el-button
              type="success" icon="el-icon-location" size="mini"
              @click="showProgressBox(scope.row)"
              ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        background
        layout="total, sizes, prev, pager, next, jumper"
        :page-sizes="[1, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        @size-change="handleSizeChange"
        :current-page="queryInfo.pagenum"
        @current-change="handleCurrentChange"
        :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
      >

      <!--  -->
      <el-form
      :model="addressFrom"
      :rules="addressFromRules"
      ref="addressFromRef"
      label-width="100px"
      >
        <!--  -->
        <el-form-item label="/:" prop="address1">
          <el-cascader
            v-model="addressFrom.address1"
            :options="cityData"
            :show-all-levels="false"
            clearable
          ></el-cascader>
        </el-form-item>
        <el-form-item label=":" prop="address2">
          <el-input v-model="addressFrom.address2"></el-input>
        </el-form-item>
      </el-form>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
      >

      <!--  -->
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './cityData.js'

export default {
  name: 'Orders',
  props: {

  },
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      ordersList: [],
      // 
      addressDialogVisible: false,
      addressFrom: {
        address1: [],
        address2: ''
      },
      addressFromRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityData,
      // 
      progressDialogVisible: false,
      progressInfo: []

    }
  },
  created () {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList () {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ordersList = res.data.goods
      this.total = res.data.total
    },

    //  pagesize 
    handleSizeChange (NewSize) {
      this.queryInfo.pagesize = NewSize
      this.getOrdersList()
    },

    //   
    handleCurrentChange (NewPage) {
      this.queryInfo.pagenum = NewPage
      this.getOrdersList()
    },

    // 
    async showProgressBox (role) {
      // 
      const id = 1106975712662
      const { data: res } = await this.$http.get(
        `/kuaidi/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = res.data
      // 
      this.progressDialogVisible = true
    }
  }
}
</script>
<style  scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size: 12px;
}
.box-card{
  box-shadow:  0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
.el-table{
  font-size: 12px;
  margin: 15px 0;
}
.el-cascader {
    display: block;
    position: relative;
    font-size: 14px;
    line-height: 40px;
}
</style>
